<script>
import { mapState } from "vuex";

import Banamex from "./components/bancos/Banamex.vue";
import Banorte from "./components/bancos/Banorte.vue";
import Banregio from "./components/bancos/Banregio.vue";
import Hsbc from "./components/bancos/Hsbc.vue";
import Santander from "./components/bancos/Santander.vue";
import Scotiabank from "./components/bancos/Scotiabank.vue";

export default {
  name: "App",
  components: {
    Banamex,
    Banorte,
    Banregio,
    Hsbc,
    Santander,
    Scotiabank
  },
  data() {
    return {
      imagenesDeBancos: {
        banamex: "logo-banamex.gif",
        banorte: "logo-banorte.gif",
        banregio: "logo-banregio.gif",
        hsbc: "logo-hsbc.gif",
        santander: "logo-santander.gif",
        scotiabank: "logo-scotiabank.gif"
      },

      tiposDeIngreso: [
        "Asalariado",
        "Profesionista Independiente",
        "Independiente",
        "Socio de Empresa",
        "Negocio Propio"
      ],

      productoSeleccionado: "Comprar mi Casa",
      tipoDeIngresoSeleccionado: "",
      valorDelInmueble: "",
      valorDelCredito: 0,

      plazoSeleccionado: "2",
      tipoDePagoSeleccionado: 0,

      montoDeCreditoPorModificacion: undefined,
      modificarMontoDeCredito: false,

      ingresoMensual: "",

      infoMontoDeCredito: false
    };
  },
  methods: {
    validarModificacionDeMontoCredito() {
      if (
        this.montoDeCreditoPorModificacion == undefined ||
        this.montoDeCreditoPorModificacion == "" ||
        this.montoDeCreditoPorModificacion == 0
      ) {
        this.modificarMontoDeCredito = false;
        this.montoDeCreditoPorModificacion = undefined;
      } else {
      }
    }
  },
  computed: {
    bancosPro() {
      return this.bancos.sort((a, b) => {
        return (
          parseFloat(a.valores.mensualidad) - parseFloat(b.valores.mensualidad)
        );
      });
    },
    creditoPreautorizado() {
      if (this.tipoDeIngresoSeleccionado == "Asalariado") {
        return parseFloat(this.ingresoMensual) * 50;
      } else {
        return parseFloat(this.ingresoMensual) * 36;
      }
    },

    montoDeCreditoMaximoFicitcio() {
      return this.valorDelInmueble * 0.9;
    },

    ...mapState(["bancos"])
  },
  mounted() {
    setInterval(() => {
      this.$forceUpdate();
    }, 10);
  }
};
</script>
